.App {
  text-align: center;
  overflow: hidden;
  z-index: 1;
}

.App-logo {
  height: 15vmin;
  pointer-events: none;
  position: absolute;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: Login-hover infinite 30s linear;
  }
}

.App-main {
  height: 10vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: left;
  font-size: calc(10px + 2vmin);
  justify-content: flex-start;
}

.App-main {
  border-bottom: 1px solid #d9dadc;
  overflow-x: hidden;
  margin-bottom: 30px;
  background-color: rgba(255, 0, 0, 0);
  height: 90vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  justify-content: flex-start;
}

.App-link {
  color: #61dafb;
}

.main-wrapper {
  width: 100%;
  height: 95%;
  padding-bottom: 5%;
  margin: auto;
  box-sizing: border-box;
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
}
/*App header*/
.App-header {
    height: 11%;
}

#logo {
    height: 100%;
}

#logo_text {
    height: 100%;
}

#account_image {
    height: 100%;
    float: right;
}

/*Next player (recommendaitons)*/
.next-wrapper {
    width: 100%;
    display: 100%;
}

.next-playing__img img {
    float: left;
    max-width: 150px;
    width: 33%;
}

.next-playing__side {
    float: left;
    margin: auto;
    padding: 10px;
}

.next-playing__name {
    font-size: 0.5em;
    margin-bottom: 0.1em;
    color: #1e1b96;
}

.next-playing__artist {
    font-size: 0.7em;
    margin-bottom: 0.1em;
    color: #1e1b96;
}

#nextPlayer {
    display: block;
    width: 100%;
}

/*Song search*/
.Search {
    padding: 30px;
    margin: auto;
    max-width: 800px;
    color: black;
    padding-bottom: 50px;
}

/*Settings*/
.settings_header {
    background-color: #eee;
    color: #444;
    cursor: pointer;
    padding: 18px;
    width: 100%;
    border: none;
    text-align: left;
    outline: none;
    font-size: 15px;
}
    .active,
    .settings_header:hover {
        background-color: #ccc;
    }

.settings {
    padding: 0 18px;
    display: none;
    overflow: hidden;
    background-color: #f1f1f1;
}

/*CurrentPlayer*/
#currentPlayer {
    height: 30%;
    padding-top: 25px;
}

canvas {
    display: block;
    vertical-align: bottom;
}
/** Now Playing **/
.now-playing__name {
    font-size: 1.5em;
    color: #1e1b96;
}

.now-playing__artist {
    margin-bottom: 1em;
    color: #1e1b96;
}

.now-playing__status {
    margin-bottom: 1em;
    color: #1e1b96;
}

.now-playing__img {
    float: left;
    text-align: right;
    width: 50%;
    display: flex;
    margin: auto;
    flex-direction: column;
}

    .now-playing__img img {
        max-width: 400px;
        width: 100%;
        margin: auto;
    }

.now-playing__side {
    float: left;
    margin: auto;
    width: 50%;
}
/** Progress **/
.progress {
    border: 1px solid #000;
    height: 10px;
    border-radius: 3px;
    overflow: hidden;
}

.progress__bar {
    background-color: #1e1b96;
    height: 10px;
}

#queue {
    max-height: 100%;
}

#buttons {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
}

#buttons_two {
    flex-direction: column;
    display: flex;
    height: 100px;
}

.button_img {
    width: 33%;
    margin: auto;
}

.ant-list-item-meta-avatar {
    height: 100%;
    margin-right: auto;
}

.ant-list-item-meta-content {
    width: auto;
}
/* width */
::-webkit-scrollbar {
    width: 8px;
    height: 20px;
}

/* Track */
::-webkit-scrollbar-track {
    /* box-shadow: inset 0 0 5px grey; */
    /* border-radius: 10px; */
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #0005;
    /* border-radius: 8px; */
}

    /* Handle on hover */
    ::-webkit-scrollbar-thumb:hover {
        background: #0005;
        /*background: #1E1B96;*/
    }
/** Now Playing **/
.now-playing__name {
    font-size: calc(18px + 2.5vw);
    color: purple;
    height: 10%;
    margin: auto;
    text-align: center;
    vertical-align: middle;
    line-height: 1.5vw;
}

.now-playing__artist {
    font-size: calc(12px + 1.5vw);
    margin-bottom: 0em;
    color: purple;
    height: 5%;
    text-align: center;
    vertical-align: middle;
    line-height: 1vw;
}

.now-playing__img {
    float: left;
    text-align: right;
    width: 50%;
    display: flex;
    margin: auto;
    flex-direction: column;
}

    .now-playing__img img {
        max-width: 400px;
        width: 100%;
        margin: auto;
    }

.now-playing__side {
    float: left;
    margin: auto;
    height: 95%;
    min-width: 80%;
}


.ant-card {
  overflow: scroll;
}

.btn--loginApp-link {
  font-size: 50px;
  margin-top: 130px;
  background-color: none;
  border-radius: 10px;
  padding: 2px;
  color: #000000;
  font-family: "Roboto", sans-serif;
  z-index: 1;
}

.btn--loginApp-link:hover {
  color: #1e1b96;
}
.btn--loginApp-link:active {
  color: #ffffff;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes Login-hover {
  0% {
    -webkit-transform: translate(5%, 0%);
    transform: translate(75%, 0%) rotate(0deg);
  }
  25% {
    -webkit-transform: translate(-10%, 100%);
    transform: translate(-200%, 500%) rotate(90deg);
  }
  50% {
    -webkit-transform: translate(-5%, 200%);
    transform: translate(-100%, 1000%) rotate(180deg);
  }
  75% {
    -webkit-transform: translate(10%, 100%);
    transform: translate(200%, 500%) rotate(270deg);
  }
  100% {
    -webkit-transform: translate(5%, 0%);
    transform: translate(75%, 0%) rotate(360deg);
  }
}

#particles-js {
  position: absolute;
  width: 100%;
  height: 100vh;
  background-color: #ffffff;
  /*background-color: #1dbfaf;*/
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  z-index: -2;
}

#refresh_btn {
  width: 100%;
  float: left;
  margin: auto;
  max-width: 150px;
  max-height: 150px;
}

#refresh_btn_div {
